/*Content -----------------------------------------------*/
.picWrapper {
  width: 35%;
  margin: 0 auto;
}

.emeliePic {
  border: 10px solid $white;
  border-radius: 50%;
  box-shadow: 2px 2px 15px $orange;
  margin: 10rem 0 4rem 0;
}

section {
  background-color: $white;
  margin: 2em 0;
  padding: 2em;
  border-radius: 15px;

  border-left: 10px solid $orange;
  border-right: 10px solid $orange;
}
