/*General -----------------------------------------------------------*/
body {
  background-color: $darkblue;
  background-image: url("../../pics/web4.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: var(--white);
  font-family: Lato, Arial, Helvetica, sans-serif;
  line-height: 2.2rem;
}

main {
  width: 50%;
  margin: 5em auto;
}

html {
  scroll-behavior: smooth;
}
/*Header*/
header {
  background-color: $white;
  width: 100%;

  box-shadow: 2px 2px 15px $orange;
  border-top: 10px solid $darkblue;

  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

/*Logo*/
.logo {
  width: 10%;
  margin: 0 auto;
}

.logoPic {
  display: block;
  height: 50px;
  margin: 0 auto;
}

.responsive {
  display: block;
  width: 100%;
  height: auto;
}

/*Footer*/
footer {
  background-color: $white;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding: 1em 0;
}

footer div {
  display: flex;
  justify-content: center;
  margin: 0.5em 1em;
}

/*Links*/
a {
  text-decoration: none;
  color: $orange;
}

a:hover {
  color: $lightorange;
}
