/*media queries -------------------------------------------------------------------*/

/*Screens no bigger than 1850px*/
@media screen and (max-width: 1850px) {
  main {
    width: 80%;
  }

  .picWrapper {
    width: 40%;
  }

  .nav-links {
    width: 50%;
  }
}

/*Screens no bigger than 700px*/
@media screen and (max-width: 700px) {
  main {
    width: 100%;
  }

  .picWrapper {
    width: 60%;
  }

  .nav-links {
    width: 75%;
  }

  /*Table*/
  th {
    font-size: 0.8rem;
  }

  td {
    font-size: 0.7rem;
  }
}
